<template>
  <div style="background:#E7ECF3;min-height:100vh;">
    <button type="button" class="btn btn-sm btn-primary rounded-circle" 
    style="position:fixed;right:10px;bottom:50px;width:50px;height:50px;font-size:21px;"
    @click="show = !show">
      +
    </button>
    <br>
    <div class="container">
      <div class="row">
        <div class="col-12">
          <div class="bg-white rounded-lg shadow-lg" style="height:100px;">
            <img src="@/static/logobtn2.png" @click="addnotif()" alt="" style="position:absolute;width:150px;right:20px;"
            class="p-3 ">
            <img src="@/static/logobumn.png" alt="" style="position:absolute;width:130px;left:20px;top:5px;"
            class="p-3 ">
            <p class="text-center font-bold text-xl p-3 pt-5 font-times font-italic">JADWAL AGENDA</p>
            <br>
            <hr class="style13" >
          </div>
        </div>
      </div>
    </div>
    <div style="width: 100%;background:white;z-index:100;" class="flex-center " v-if="show">
      <div
        class="rounded-lg shadow-lg bg-blue-600"
        style="
          width: 90vw;
          position: absolute;
          z-index: 1000;
          background: white;
          top: 4vh;
        "
      >
        <div class="p-4 animate__animated animate__fadeIn shadow-lg rounded-lg" style="background:white;">
          <button type="button" class="btn btn-sm btn-danger float-right " @click="show=false">x</button>
          <p class="text-xl font-bold font-times">TAMBAH AGENDA</p>
          <hr class="style13" />
          <div class="sm-form">
            <label for="title">Judul Agenda</label>
            <input
              type="text"
              id="title"
              name="title"
              class="form-control form-control-sm"
              placeholder="title"
              v-model="vdata.title"
            />
          </div>
          <div class="sm-form">
            <p>Isi Agenda :</p>
            <textarea
              type="text"
              id="isi"
              name="isi"
              rows="2"
              placeholder="isi..."
              class="form-control md-textarea"
              v-model="vdata.isi"
            ></textarea>
          </div>
          <div class="sm-form">
            <label for="tanggal">Tanggal : </label>
            <input
              type="date"
              id="tanggal"
              name="tanggal"
              class="form-control form-control-sm"
              placeholder="tanggal"
              v-model="vdata.tanggal"
            />
          </div>
          <div class="sm-form">
            <label for="jam">Jam : </label>
            <input
              type="time"
              id="jam"
              name="jam"
              class="form-control form-control-sm"
              placeholder="jam"
              v-model="vdata.jam"
            />
          </div>
          <hr class="style13" />
          <button type="button" @click="add()" class="btn btn-sm btn-primary">
            ADD +
          </button>
           <button type="button" @click="show=false" class="btn btn-sm btn-danger float-right">
            Close
          </button>
        </div>
      </div>
    </div>
    <div style="width: 100%;background:white;z-index:100;" class="flex-center " v-if="show2">
      <div
        class="rounded-lg shadow-lg bg-blue-600"
        style="
          width: 90vw;
          position: absolute;
          z-index: 1000;
          background: white;
          top: 4vh;
        "
      >
        <div class="p-4 animate__animated animate__fadeIn shadow-lg rounded-lg" style="background:white;">
          <button type="button" class="btn btn-sm btn-danger float-right " @click="show2=false">x</button>
          <p class="text-xl font-bold font-times">ISI AGENDA</p>
          <br>
          <hr class="style13">
          <tr class="">
            <td style="font-family:times new roman;font-size:18px;">Judul</td>
            <td style="font-family:times new roman;font-size:18px;" class="pl-2 pr-2">&nbsp;:&nbsp;</td>
            <td style="font-family:times new roman;font-size:18px;" class="font-bold">{{datanya.title}}</td>
          </tr>
          <tr class="">
            <td style="font-family:times new roman;font-size:18px;">Isi Agenda</td>
            <td style="font-family:times new roman;font-size:18px;" class="pl-2 pr-2">&nbsp;:&nbsp;</td>
            <td style="font-family:times new roman;font-size:18px;" class="shadow-lg rounded-lg p-2">{{datanya.isi}}</td>
          </tr>
          <tr class="">
            <td style="font-family:times new roman;font-size:18px;">Tanggal</td>
            <td style="font-family:times new roman;font-size:18px;" class="pl-2 pr-2">&nbsp;:&nbsp;</td>
            <td style="font-family:times new roman;font-size:18px;">{{format(datanya.tanggal)}}</td>
          </tr>
          <tr class="">
            <td style="font-family:times new roman;font-size:18px;">Jam</td>
            <td style="font-family:times new roman;font-size:18px;" class="pl-2 pr-2">&nbsp;:&nbsp;</td>
            <td style="font-family:times new roman;font-size:18px;">{{datanya.jam}}</td>
          </tr>
          <hr class="style13">
          <button type="button" v-if="datanya.status=='1'" @click="datang(datanya.id);show2=false;agenda='datang'" class="btn btn-sm btn-primary  float-right">Datang <span class="typcn typcn-arrow-right"></span> </button>
          <button type="button" v-if="datanya.status=='2'" @click="masuk(datanya.id);show2=false;agenda='masuk'" class="btn btn-sm btn-primary  float-left"><span class="typcn typcn-arrow-left"></span> Masuk</button>
          <button type="button" v-if="datanya.status=='2'" @click="keluar(datanya.id);show2=false;agenda='keluar'" class="btn btn-sm btn-danger  float-right">Keluar <span class="typcn typcn-arrow-right"></span> </button>
          <button type="button" v-if="datanya.status=='3'" @click="datang(datanya.id);show2=false;agenda='datang'" class="btn btn-sm btn-primary  float-right"> <span class="typcn typcn-arrow-left"></span> Datang </button>
          <br>
          <br>
        </div>
      </div>
    </div>
    <div class="container" style="z-index:1;">
      <div class="row">
        <div class="col-12">
          <div class="row p-2">
            <div :class="agenda=='masuk'?'bg-blue-300 text-black':'bg-white'" @click="agenda='masuk'" class="col-4  rounded-lg shadow-lg p-2 text-center font-bold font-times hover:bg-blue-500">
              <span class="typcn typcn-pin-outline"></span> MASUK
            </div>
            <div :class="agenda=='datang'?'bg-blue-300 text-black':'bg-white'" @click="agenda='datang'" class="col-4  rounded-lg shadow-lg p-2 text-center font-bold font-times hover:bg-blue-500">
              <span class="typcn typcn-adjust-brightness"></span> DATANG
            </div>
            <div :class="agenda=='keluar'?'bg-blue-300 text-black':'bg-white'" @click="agenda='keluar'" class="col-4  rounded-lg shadow-lg p-2 text-center font-bold font-times hover:bg-blue-500">
             <span class="typcn typcn-download-outline"></span> KELUAR
            </div>
          </div>
        </div>
        <div class="col-12" v-if="agenda=='masuk'">
          <div class="bg-white rounded-lg shadow-lg p-2 text-center">

          <h3>AGENDA MASUK</h3>
          
            <div
              class="list-group-item cursor-pointer hoverbg anim text-left pt-1 pl-3"
          style="z-index:1;"
            @click="showAgenda(element)"
              v-for="(element, index) in lists1"
              :key="element.title"
            >
            <p class="font-bold font-times text-md">
              {{ element.title }} 
            </p>
            <p class="text-sm font-italic">
              {{format(element.tanggal)}} <span class="float-right">{{element.jam}}</span>
            </p>
            </div>
          </div>
        </div>
        <div class="col-12"  v-if="agenda=='datang'">
          <div class="bg-white rounded-lg shadow-lg p-2 text-center">
          <h3>AGENDA AKAN DATANG</h3>
            <div
              class="list-group-item cursor-pointer hoverbg anim text-left pt-1 pl-3"
          style="z-index:1;"
            @click="showAgenda(element)"
              v-for="(element, index) in lists2"
              :key="element.title"
            >
            <p class="font-bold font-times text-md">
              {{ element.title }} 
            </p>
            <p class="text-sm font-italic">
              {{format(element.tanggal)}} <span class="float-right">{{element.jam}}</span>
            </p>
            </div>
          </div>
        </div>
        <div class="col-12"  v-if="agenda=='keluar'">
          <div class="bg-white rounded-lg shadow-lg p-2 text-center">
          <h3>AGENDA KELUAR</h3>
            <div
              class="bg-red-400 list-group-item cursor-pointer hoverbg anim text-left pt-1 pl-3"
          style="z-index:1;"
              v-for="(element, index) in lists3"
              :key="element.title"
            >
            <span class="float-right font-bold font-italic hoverred" @click="hapus(element.id,index)">Delete</span>
            <p class="font-bold font-times text-md" @click.prevent="showAgenda(element)">
              {{ element.title }} 
            </p>
            <p class="text-sm font-italic" @click.prevent="showAgenda(element)">
              {{format(element.tanggal)}} <span class="float-right">{{element.jam}}</span>
            </p>
            </div>
          </div>
        </div>
        <rawDisplayer class="col-3" :value="list1" title="List 1" style="z-index:1;"/>
        <rawDisplayer class="col-3" :value="list2" title="List 2" style="z-index:1;"/>
        <rawDisplayer class="col-3" :value="list3" title="List 3" style="z-index:1;"/>
      </div>
    </div>
  </div>
</template>
<script>
var id = require("date-fns/locale/id");
import draggable from "vuedraggable";
import autophp from '@/plugins/autophp.js'
let sdb = new autophp()
import firebase from 'firebase'
let db = firebase.firestore();
import axios from "axios";
import uniqid from "uniqid";
export default {
  name: "two-lists",
  display: "Two Lists",
  order: 1,
  components: {
    draggable,
  },
  data() {
    return {
      show: false,
      show2: false,
      agenda:"masuk",
      datanya:[],
      regis:"",
      vdata: {},
      list1: [],
      list2: [],
      list3: [],
    };
  },
  computed: {
    lists1(){
      let data = this.list1 
      data = data.sort(this.sortFunction)
      return data
    },
     lists2(){
      let data = this.list2 
      data = data.sort(this.sortFunction)
      return data
    },
     lists3(){
      let data = this.list3 
      data = data.sort(this.sortFunction)
      return data
    },
  },
  methods: {
    sortFunction(a,b){  
      var dateA = new Date(a.tanggal).getTime();
      var dateB = new Date(b.tanggal).getTime();
      return dateA > dateB ? 1 : -1;  
    }
  },
  mounted() {
    let that = this;
    db.collection('listagenda').onSnapshot(res=>{
      let data = res.docs.map(e=>{
        return {
          id:e.id,
          ...e.data()
        }
      })
        console.log('agenda',data)
           that.datanya=data
           let arr1=[]
           let arr2=[]
           let arr3=[]
           that.datanya.forEach(e=>{
             if(e.status==1){
               arr1.push(e)
             }else if(e.status==2){
               arr2.push(e)
             }else if(e.status==3){
               arr3.push(e)
             }
           })
           that.list1=arr1
           that.list2=arr2
           that.list3=arr3
    })
    document.addEventListener('keyup',function(e){
      if(e.key=="Escape"){
        that.show=false
        that.show2=false
      }
    })
  },
  methods: {
    hapus(id,index){
      console.log(id)
      this.show=false;
      this.show2=false;
      db.collection(`listagenda`).doc(id).delete().then(res=>{

      })
    },  
    addnotif(){
      let that= this;
      document.addEventListener(
      "deviceready",
      function() {
        var push = PushNotification.init({
          android: {
            senderID: "344034493973", // setting project firebase
            sound: "true",
            vibrate: "true",
            clearNotifications: false, // clear notifications from shade on app start
            forceShow: true
          },
          ios: {
            alert: "true",
            badge: "true",
            sound: "true"
          }
        });
        PushNotification.createChannel(() => {
          console.log('success');
        }, () => {
          console.log('error');
        }, {
          id: "test-channel",
          description: "My first test channel",
          importance: 5,
        vibration: [500, 1000, 500, 500],
          sound:'test', // test adalah test.wav nama file sound notification
        });
        push.on("registration", function(data) {
          // ! AMBIL data registrationId untuk ditaruh sebagai payload to : ""
          // ! simpan data regitrationId kedatabase
          localStorage.setItem("notifid", data.registrationId);
          that.regis = data.registrationId;
          db.collection('agendanotif').get().then(res=>{
            let data = res.docs.map(e=>{
              return {...e.data()}
            })
            data = data.filter(e=>{
              if(e.fcm==that.regis){
                return e  
              }
            })
            if(data.length>0){
            }else{
              db.collection('agendanotif').doc().set({fcm:that.regis},{merge:true}).then(res=>{
              })
            }
          })
          that.$forceUpdate()
          // Simpan data.registrationId KE DATABASE DENGAN GANDENGAN ID USER untuk di tembak dengan payload {to : ""} firebase
        });
        push.on("notification", function(data) {});
        push.on("error", function(e) {
          console.log(e.message);
        });
      },
      false
    );
    },
    esc(e){
      console.log(e)
    },
    showAgenda(data){
      this.datanya=data;
      this.show2=true;
      this.$forceUpdate();
    },
    format(date) {
      return this.$datefns.format(
        new Date(date),
        "dddd MMMM YYYY",
        {
          locale: id
        }
      );
    },
    notif(id){
      db.collection("agendanotif")
        .get()
        .then((res) => {
          let data = res.docs.map((e) => {
            return {
              ...e.data(),
            };
          });
          console.log(data);
          data.forEach((e,i) => {
            console.log(i)
            let datas = {
              to:
                e.fcm,
              notification: {
                title: `${id == '1' ? 'AGENDA MASUK': id=='2'? 'AGENDA AKAN DATANG' : 'AGENDA KELUAR'}`,
                body: `INFO ADA ${id == '1' ? 'AGENDA MASUK': id=='2'? 'AGENDA AKAN DATANG' : 'AGENDA KELUAR'}`,
                param1: "test",
                param2: "ok",
                priority: 10,
                sound: "default",
                ledColor: [255, 255, 128, 16],
                vibrationPattern: [250, 1000, 250, 500]
              },
            };
            datas = JSON.stringify(datas);
            axios
              .post("https://fcm.googleapis.com/fcm/send", datas, {
                headers: {
                  Authorization:
                    "key=AAAAUBoNxhU:APA91bGmp6193yTOWq0XcUu4adMjbF4XVk2uqDy8V-1y7pT26S_Q1kVkYTSyLBsgU-B4lUyYDMykMKtuiQWlVeYIEr713d5Ty0GphyUxcGlMb1D1uKsOPZ4LujfYzke2qdnZOBYEMzdm",
                  "Content-Type": "application/json",
                },
              })
              .then((res) => {
                console.log(res);
                this.$forceUpdate();
              });
          });
        });
    },
    add: function () {
      db.collection(`listagenda`).doc().set({...this.vdata,status:1},{merge:true}).then(res=>{
          console.log('berhasil')
          this.show=false;
          this.vdata={}
          this.$forceUpdate();
          this.notif('1')
      }).catch(err=>{
          console.log(err.message)
      })
    },
    masuk(id){
      db.collection(`listagenda`).doc(id).set({status:1},{merge:true}).then(res=>{
          console.log('berhasil')
          this.notif('1')
      }).catch(err=>{
          console.log(err.message)
      })
    },
    datang(id){
      console.log('id',id)
      db.collection(`listagenda`).doc(id).set({status:2},{merge:true}).then(res=>{
          console.log('berhasil')
          this.notif('2')

      }).catch(err=>{
          console.log(err.message)
      })
    },
    keluar(id){
      db.collection(`listagenda`).doc(id).set({status:3},{merge:true}).then(res=>{
          console.log('berhasil')
      }).catch(err=>{
          console.log(err.message)
      })
    },
    replace: function () {
      this.list = [{ name: "Edgard" }];
    },
    clone: function (el) {
      return {
        name: el.name + " cloned",
      };
    },
    log: function (evt) {
      let id1=[]
      let id2=[]
      let id3=[]
      this.list1.forEach(e=>{
        id1.push(e.id)
      })
      this.list2.forEach(e=>{
        id2.push(e.id)
      })
      this.list3.forEach(e=>{
        id3.push(e.id)
      })
      if(id1.length>0){
        console.log(id1)
        // sdb.collection("list").doc().select(`update list set status=1 where id in (${id1.toString()})`).then(res => {
        //     console.log(res);
        // });
      }
      if(id2.length>0){
        console.log(id2)
        // sdb.collection("list").doc().select(`update list set status=2 where id in (${id2.toString()})`).then(res => {
        //     console.log(res);
        // });
      }
      if(id3.length>0){
        console.log(id3)
      // sdb.collection("list").doc().select(`update list set status=3 where id in (${id3.toString()})`).then(res => {
      //       console.log(res);
      //   });
      }
        // socket.emit("refresh", null);
    },
  },
};
</script>
<style>
.flex-center {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}
.hoverbg:hover{
  background: lightblue;
}
.hoverred:hover{
  color:red;
  text-decoration: underline;
}

</style>